/* ============Custom stepper styles================ */
.stepperSpace {
    position: relative !important;
}

.stepperSpace .MuiStepLabel-label {
    padding-left: 15px !important;
}

.stepperContainer {
    text-align: left;
}

.stepperContentSpace {
    padding-left: 35px !important;
}

.stepperContainer .stepperContentSpace:not(.failed) {
    border-left: 1px solid #1976d2 !important;
}

.stepperLoader {
    position: absolute;
    left: -8px;
    top: 0;
}

.stepperContainer>.MuiStepConnector-vertical {
    display: none;
}

.stepperContainer>.MuiStep-vertical>.MuiStepConnector-vertical.Mui-completed>.MuiStepConnector-line {
    border-left: 1px solid #1976d2 !important;
}

.stepperContainer>.MuiStep-vertical>.MuiStepConnector-vertical.Mui-active:not(.failed)>.MuiStepConnector-line {
    border-left: 1px solid #1976d2 !important;
}

.stepperContainer .stepperSpace>.Mui-completed>.MuiSvgIcon-root {
    color: #1976d2 !important;
    border: 0px solid #1976d2 !important;
}

.stepperContainer .stepperSpace.skippedStep>.Mui-completed>.MuiSvgIcon-root {
    color: lightgrey !important;
    border: 0px solid lightgrey !important;
}

.stepperSpace.skippedStep .MuiStepLabel-label {
    color: lightgrey !important;
}

.stepperContainer .stepperSpace:not(.failed)>.Mui-active>.MuiSvgIcon-root {
    color: transparent !important;
    border: 2px solid #1976d2 !important;
    border-radius: 50% !important;
}

.stepperContainer .stepperSpace>.Mui-disabled>.MuiSvgIcon-root {
    color: transparent !important;
}

/* ============End Custom stepper styles================ */

/* ============Properties styles================ */
.error-border .MuiOutlinedInput-notchedOutline {
    border: 1px solid red;
}

.errorAutocomplete .MuiInputBase-root .MuiOutlinedInput-notchedOutline {
    border-color: red !important;
}

.datatimeInputCls:not(.Create) .MuiOutlinedInput-notchedOutline {
    border-color: rgba(0, 0, 0, 0.23) !important;
}

.datatimeInputCls:not(.Create) .Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #1976d2 !important;
}

/* ============End Properties styles================ */

.partsTableSty {
    height: 450px;
}

.height100Per {
    height: 100% !important;
}